import Card from '../components/business/Card/'
import EditorInput from '../components/business/EditorInput/'

export const contentMixin = {
    data() {
        return {
            isEdit: false,                       // 
        }
    },
    props: {
        form: {
            type: Object,
            require: true
        },
        state: 0,// 0  1 
    },
    methods: {
        save() {
            if (this.validate()) {
                this.message('', 'warning')
                return
            }
            this.setRequestData()
            let url
            if (this.state === this.handleType['add']||this.state ===this.handleType['copy']) {  // 
                url = this.addUrl
            } else {
                url = this.updateUrl
            }
            this.request.postJson(url, this.selfForm)
                .then(res => {
                    if (res.data.code === 200) {
                        this.message('')
                        if (this.state === this.handleType['add']||this.state ===this.handleType['copy']) {
                            this.$emit('handleAdd', res.data.data)
                        }else{
                            this.$emit('handleUpdate', res.data.data)
                        }
                        this.isEdit = false
                    }
                }).catch(err => {
                this.isEdit = false
                this.message('', 'error')
            })
        },
        setRequestData(){},
        message(content, type) {
            this.$message({
                message: content || '',
                type: type || 'success'
            })
        },
        changeEditState(){
            this.isEdit=true
        },

    },
    components: {
        Card, EditorInput
    }
}

